@use "../base/reset";
@use "../base/layout";
@use "../base/colors";
@use "../base/typography";
@use "../components/header";
@use "../components/testimonials";
@use "../components/footer";
@use "../components/ui/form.scss";

.hero {
  position: relative;
  top: -90px;
  height: 648px;
  padding-top: 161px;
  background-image: url("../assets/images/rodan-can.jpeg");
  background-size: cover;
  background-position: 50% 61%;
  z-index: 1;

  &__about-us {
    position: absolute;
    bottom: -70px;
    left: 30px;
  }

  &__protectors {
    position: absolute;
    top: -410px;
    right: -360px;
    transform: scale(0.6);
  }

  &::before {
    content: "";
    position: absolute;
    inset: 0;
    background: rgba(30, 30, 30, 0.7);
    z-index: 1;
  }

  .hero-content {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    z-index: 2;
    padding-left: 100px !important;

    @include layout.max-width;

    .hero-title {
      @include typography.desktop-h1-h-1;
      color: #f8f7fc;
      margin-bottom: 32px;
      z-index: 3;
    }

    .hero-subtitle {
      color: colors.$color-white;
      @include typography.desktop-bt1-18-r;
      margin-bottom: 56px;
      max-width: 554px;
      z-index: 3;
    }
  }

  .bottom-block {
    height: 74px;
    width: 100%;
    position: absolute;
    z-index: 3;
    right: calc(-50%);
    bottom: -8px;
    background-color: colors.$color-bg-light;

    img {
      height: 101%;
      transform: translateX(calc(-100% + 4px));
    }
  }

  .car-models {
    position: absolute;
    bottom: -70px;
    left: calc(50% - 150px);
    z-index: 3;

    display: flex;

    & img:nth-child(2n) {
      margin-left: 12px;
    }

    & img:nth-child(2n + 1) {
      margin-left: -38px;
    }
  }
}

.numbers-section {
  &__delta-motors {
    position: absolute;
    top: -140px;
    right: 200px;
    z-index: 3;
  }

  &__container {
    position: relative;
    padding: 94px 0 220px;
    @include layout.max-width;
  }

  &__grid {
    display: flex;
    justify-content: center;
    position: relative;
    z-index: 4;
    gap: 45px;
  }

  .numbers-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-width: 366px;

    .numbers-value {
      @include typography.number-value;
      color: #455c7a4d;
    }

    .numbers-label {
      @include typography.desktop-sb2-28-b;
    }
  }

  .numbers-divider {
    height: 102px;
    width: 1px;
    background-color: colors.$color-accent-1;
  }

  &__vector-arrows {
    position: absolute;
    top: -70px;
    left: 0;
    z-index: 3;
  }

  &__numbers {
    position: absolute;
    bottom: 80px;
    right: 0;
  }
}
